.header {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 120px;
}
.header-logo {
  height: 27px;
  max-width: 140px;
  margin-right: 24px;
}
.absolute-center {
  padding-top: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-right {
  display: flex;
  flex: 1;
  justify-content: space-between;
  align-items: center;
  height: 72px;
}

.header-left-search-container{
  height: 54px;
  display: flex;
  align-items: center;
  border-radius: 2px;
  box-shadow: var(--primary-box-shadow);
  border: 1px solid var(--border-white);
  background-color: var(--primary-bg);
  width: 70%;
}

.location-wrapper{
    display: flex;
    flex: 1;
    justify-content: space-between;
    padding: 0px 10px;
}


.location-icon-name{
    display: flex;
    color: var(--zomato-gray);
}
.location-icon{
    color: var(--zomato-red);
    font-size: 20px;
    margin-right: 8px;
}

.location-search-seprator{
    height: 20px;
    border:0.5px solid var(--gray);
}

.header-searchbar{
    display: flex;
    /* margin-right:8px; */
    flex: 2;

}
.search-icon{
    font-size: 20px;
    color: var(--zomato-gray);
    margin: 0px 12px;
}
.search-input{
    border: none;
    outline: none;
    width: 60%;
    font-size: 14px;
}

.profile-wrapper{
    display: flex;
    justify-content: space-between;
    width: 15%;
    align-items: center;
    /* font-size: 30px; */
}
.header-profile-image{
    height: 30%;
    width: 30%;
    border-radius: 100%;
}

.header-username{
    font-size: 16px;
    font-weight: 600;    
}

.profile-options-icon{
    font-size: 20px;
}